@import "vars";

@media only screen and (max-width : 1200px) {
  .header {

   &__tel {
    display: none; }

   &__view {
     align-items: flex-start; }


   .img-responsive {
    margin-top: 0; }

   &__title {

    margin-top: 100px;
    margin-right: 10px; }

   &__text {
    font-size: 30px; }

   &__navigation {
    margin-right: 0; }

   .portfolio__link {
    height: 250px; }

   .portfolio__title {
    line-height: 18px;
    font-size: 15px; } }

  .services {

    padding-top: 50px;
    padding-bottom: 50px;

    &__wrap {
     margin-top: 10px; }

    &__item {
     justify-content: center;

     padding-top: 50px;
     height: 200px; } }

  .order {
    background-position: 50% 0;





    &__subtitle {
      font-size: 25px; }

    &__description {

     font-size: 18px; } }

  .features {
    padding: 50px 0 50px 0;

    &__wrap-description {
      flex-basis: 100%; } }

  .clients {
    padding: 50px 0 50px 0; }


  .video {
    height: 200px; }

  .contact {
    margin-top: 50px;

    .wrapper {
      justify-content: center; }


    &__description {

      flex-basis: 350px;
      margin-left: 20px; }

    &__text {
      font-size: 16px; } }


  .footer {
    height: 20px; } }

@media only screen and (max-width : 920px) {
  .header {

    &__navigation {
        display: none; }

    &__view {
        flex-direction: column; }

    &__title {
        flex-basis: 50px; } }

  .hamburger,
  #mobile-menu,
  .site-overlay {
    display: inline-block; }


  .portfolio {

     &__list {
        flex-wrap: wrap; }

     &__item {
        flex: 1 1 300px; }

     &__link {
        height: 175px; } }

  .description__title {
      font-size: 25px; }

  .order {
    background-position: 50% 0;

    padding: 50px 0 50px 0;

    &__wrap-img {
      display: none; }

    &__content {
      margin-left: 100px;
      flex-basis: 100%; } }


  .contact {

    .wrapper {
      flex-direction: column; }


    &__map-container {
      flex: auto; }

    &__description {
      margin-top: 20px; } } }


@media only screen and (max-width : 768px) {
   .header {

     &__text {
       font-size: 24px; }


     .portfolio {

      &__item {

        flex-basis: 100%; } } }


   .services {


      &__item {

        flex-basis: 100%; } }

   .features {

      padding: 20px 0 20px 0;

      &__wrap-list {

        flex-direction: column; }

      &__item {
        flex-direction: column; }


      &__wrap-description {
        margin-top: 20px;
        text-align: center; }

      .wrap-icon {
        text-align: center; } }

   .clients {

     padding: 20px 0 20px 0;

     &__item {

       flex-basis: 100%; } } }











